import Vue from 'vue'
import Vuex from 'vuex'

import explicador from './explicador/index'
import shared from './shared/index'
import auth from './auth/index'

Vue.use(Vuex)

export const store = new Vuex.Store({

    modules: {
        shared: shared,
        explicador: explicador,
        auth: auth,
    }

})



