export default {
    state: {
        user: null,
        explicadores: [
            { header: 'Explicadores', icon: '<i class="fas fa-street-view"></i>' },
            { link: '/seie/profile', avatar: 'https://vuetifyjs.com/static/doc-images/lists/1.jpg', title: 'Brunch this weekend?', subtitle: "<span class='grey--text text--darken-2'>Ali Connors</span>  I'll be in your neighborhood doing errands this weekend. Do you want to hang out?" },
            { divider: true, inset: true },
            { link: '/seie/profile', avatar: 'https://vuetifyjs.com/static/doc-images/lists/2.jpg', title: 'Summer BBQ <span class="grey--text text--lighten-1">4</span>', subtitle: "<span class='grey--text text--darken-2'>to Alex, Scott, Jennifer</span>  Wish I could come, but I'm out of town this weekend." },
            { divider: true, inset: true },
            { link: '/seie/profile', avatar: 'https://vuetifyjs.com/static/doc-images/lists/3.jpg', title: 'Oui oui', subtitle: "<span class='grey--text text--darken-2'>Sandra Adams</span>  Do you have Paris recommendations? Have you ever been?" },
            { divider: true, inset: true },
            { link: '/seie/profile', avatar: 'https://vuetifyjs.com/static/doc-images/lists/4.jpg', title: 'Birthday gift', subtitle: "<span class='grey--text text--darken-2'>Trevor Hansen</span>  Have any ideas about what we should get Heidi for her birthday?" },
            { divider: true, inset: true },
            { link: '/seie/profile', avatar: 'https://vuetifyjs.com/static/doc-images/lists/5.jpg', title: 'Recipe to try', subtitle: "<span class='grey--text text--darken-2'>Britta Holt</span>  We should eat this: Grate, Squash, Corn, and tomatillo Tacos." },
            { divider: true, inset: true },
            { link: '/seie/profile', avatar: 'https://vuetifyjs.com/static/doc-images/lists/5.jpg', title: 'Good to be', subtitle: "<span class='grey--text text--darken-2'>Britta Holt</span>  We should eat this: Grate, Squash, Corn, and tomatillo Tacos." },
            { divider: true, inset: true },
            { link: '/seie/profile', avatar: 'https://vuetifyjs.com/static/doc-images/lists/5.jpg', title: 'Recipe ty', subtitle: "<span class='grey--text text--darken-2'>Britta Holt</span>  We should eat this: Grate, Squash, Corn, and tomatillo Tacos." },
        ]
    },
    mutations: {
        setUser(state, user) {
            state.user = user
        },
    },
    getters: {
        user (state) {
            return state.user
        },
        getExplicadores (state) {
            return state.explicadores
        }
    }

}


