<template>
    <div>
        <div class="text-xs-center">
            <h1 >Help Page (explicador)</h1>
            <h4>Pagina com a informacao e tutoriais do explicador</h4>
        </div>

        <template>
        <div class="text-xs-center">
            <v-btn
                    color="secondary"
                    :loading="loading"
                    @click.native="loader = 'loading'"
                    :disabled="loading"
            >
                Accept Terms
            </v-btn>

            <v-btn
                    :loading="loading3"
                    @click.native="loader = 'loading3'"
                    :disabled="loading3"
                    color="blue-grey"
                    class="white--text"
            >
                Upload
                <v-icon right dark>cloud_upload</v-icon>
            </v-btn>

            <v-btn
                    color="success"
                    :loading="loading2"
                    @click.native="loader = 'loading2'"
                    :disabled="loading2"
            >
                Custom Loader
                <span slot="loader">Loading...</span>
            </v-btn>

            <v-btn
                    color="info"
                    :loading="loading4"
                    @click.native="loader = 'loading4'"
                    :disabled="loading4"
            >
                Icon Loader
                <span slot="loader" class="custom-loader">
        <v-icon light>cached</v-icon>
      </span>
            </v-btn>
        </div>
    </template>
    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data() {
            return {
                loader: null,
                loading: false,
                loading2: false,
                loading3: false,
                loading4: false,
            }
        },
        watch: {
            loader() {
                const l = this.loader
                this[l] = !this[l]

                setTimeout(() => (this[l] = false), 3000)

                this.loader = null
            }
        },
        beforeCreate() {
            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(70)
        },
    }
</script>
<style>

</style>


