<template>
    <v-container>
        <v-card xs12 class="mb-1 explicador-bar-fixed">
            <v-list class="text-xs-center"  three-line>
                <h4>Seie > Perfil > Dados Pessoais</h4>
            </v-list>
            <v-list></v-list>
        </v-card>
        <!--Informacoes do perfil-->
        <v-card>
            <v-card-title>Perfil do explicador</v-card-title>
            <v-container grid-list-sm class="pa-4">
                <v-layout row wrap>
                    <v-flex xs12 align-center justify-space-between>
                        <v-layout align-center>
                            <v-avatar size="40px" class="mr-3">
                                <img
                                        src="//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png"
                                        alt=""
                                >
                            </v-avatar>
                            <v-text-field
                                    placeholder="Name"
                            ></v-text-field>
                        </v-layout>
                    </v-flex>

                    <v-flex xs12>
                        <v-text-field
                                prepend-icon="mail"
                                placeholder="Email"
                        ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                        <v-text-field
                                type="tel"
                                prepend-icon="phone"
                                placeholder="(000) 000 - 0000"
                                mask="phone"
                        ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                        <v-btn class="red">Actualizar senha</v-btn>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancel</v-btn>
                <v-btn flat @click="dialog = false">Save</v-btn>
            </v-card-actions>
        </v-card>

        <!--Informacoes Academicas-->
        <v-card>
            <v-card-title>Dados Academicos</v-card-title>
            <v-container grid-list-sm class="pa-4">
                <v-layout row wrap>

                    <v-flex xs12>
                        <v-text-field
                                prepend-icon="mail"
                                placeholder="Email"
                        ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                        <v-text-field
                                type="tel"
                                prepend-icon="phone"
                                placeholder="(000) 000 - 0000"
                                mask="phone"
                        ></v-text-field>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancel</v-btn>
                <v-btn flat @click="dialog = false">Save</v-btn>
            </v-card-actions>
        </v-card>

        <!--Informacoes De Loacalizacao-->
        <v-card>
            <v-card-title>Dados de Localizacao</v-card-title>
            <v-container grid-list-sm class="pa-4">
                <v-layout row wrap>

                    <v-flex xs12>
                        <v-text-field
                                prepend-icon="mail"
                                placeholder="Email"
                        ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                        <v-text-field
                                type="tel"
                                prepend-icon="phone"
                                placeholder="(000) 000 - 0000"
                                mask="phone"
                        ></v-text-field>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancel</v-btn>
                <v-btn flat @click="dialog = false">Save</v-btn>
            </v-card-actions>
        </v-card>

    </v-container>
</template>
<script>
    export default {
        data () {
            return {
                dialog: false
            }
        },
        beforeCreate () {
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
//            this.$Progress.finish()
        },
    }
</script>


