import * as firebase from 'firebase'
import router from '../../router/index'

export default {
    state: {
        userAuth: null,
        db_users: [],
    },
    mutations: {

        setUserAuth (state, user) {

            const fireUser = firebase.auth().currentUser
            state.userAuth = user

            if (user !== null) {

                state.userAuth.email = fireUser.email
                state.userAuth.emailVerified = fireUser.emailVerified

            }

        },

        setDBUser (state, users) {
            state.db_users = users
        },
    },
    getters: {
        userAuth (state) {
            return state.userAuth
        },
    },
    actions: {
        createUser ({commit}, value) {

            commit('setLoading',true)
            commit('clearError')
            let userOjectt = {}
            firebase.auth().createUserWithEmailAndPassword(value.email, value.password)
                .then(
                    fbUser => {
                        userOjectt = {
                            id: fbUser.uid,
                            name: '',
                            idade: -1,
                        }
                        // Gravar o novo user no firebase
                        firebase.database().ref('Users').push(userOjectt)
                        firebase.auth().currentUser.updateProfile({
                            displayName: "",
                            photoURL: "https://avatars0.githubusercontent.com/u/18400142?s=460&v=4"
                        })
                        return fbUser
                    }
                )
                .then((fbUser) => {

                    firebase.auth().currentUser.sendEmailVerification()
                        .then(() => {

                            commit('setUserAuth',userOjectt)
                            commit('setLoading',false)

                        })
                        .catch((error) => {
                            commit('setLoading',false)
                            commit('setError',error)
                            console.log(error)
                        })

                })
                .catch(
                    error => {
                        commit('setLoading',false)
                        commit('setError',error)
                        console.log(error)
                    }
                )

        },

        loginUser ({commit},  value,) {

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().signInWithEmailAndPassword(value.email, value.password)
                .then(
                    user => {
                        const newUser = {
                            id: user.uid,
                        }
                        commit('setUserAuth', newUser)
                        commit('setLoading', false)
                        router.push('/teste/profile')
                    }
                )
                .catch(
                    error => {
                        commit('setLoading', false)
                        commit('setError', error)
                        console.log(error)
                    }
                )

        },

        sendUserPasswordResetEmail ({commit},  value,) {

            if (value === null || value === undefined) return

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().sendPasswordResetEmail(value.email)
                .then(() => {
                    //concluido com sucesso
                    console.log('reset link enviado com sucesso')
                    commit('setLoading', false)
                })
                .catch((error) => {
                    commit('setLoading', false)
                    commit('setError', error)
                    console.log(error)
                })
        },

        updateUserPassword ({commit},  value,) {

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().signInWithEmailAndPassword(value.email, value.previousPassword)
                .then(
                    user => {
                        user.updatePassword(value.password)
                            .then((resp) => {
                                console.log('Password actualizada com sucesso')
                                commit('setLoading', false)
                            })
                            .catch((err) => {
                                commit('setLoading', false)
                                commit('setError', error)
                                console.log(error)
                            })
                    }
                )
                .catch(
                    error => {
                        commit('setLoading', false)
                        commit('setError', error)
                        console.log(error)
                    }
                )

        },

        logOutUser ({commit}) {
            firebase.auth().signOut()
            commit('setUserAuth', null)
            router.push('/')
        },
        autoSignIn ({commit}, user) {
            commit('setUserAuth', user)
        },
        getAllUsers ({commit,state}) {

            firebase.database().ref('Users').once('value')
                .then((data) => {
                    const userss = []
                    const obj = data.val()
                    for (let key in obj) {
                        userss.push({
                            id: obj[key].id,
                            idade: obj[key].idade,
                            name: obj[key].name,
                        })
                    }
                    commit('setDBUser',userss)
                    return userss
                })
                .then( list => {

                    // Verifica se o user esta autenticado, se nao estiver cancele a busca
                    if (state.userAuth === null || state.userAuth === undefined) return

                    // Buscando os dados do user do firebaseUser, para se ter acesso aos outros dados deste utilizador autenticado
                    const user = list.find(user => {
                        return user.id === state.userAuth.uid
                    })
                    if (user !== undefined && user !== null) {
                        commit('setUserAuth',user)
                    }

                })
                .catch( (error => {
                    console.log(error)
                }))
        },

    }
}


