<template>
    <v-layout row>
        <v-flex xs12>

            <v-card v-if="isHomePage">
                <v-list class="text-xs-center">
                    <h4>Explicadores Populares</h4>
                </v-list>
                <v-list three-line>
                    <template v-for="item in explicadores">
                        <v-subheader v-if="item.header" v-text="item.header"></v-subheader>
                        <v-divider v-else-if="item.divider" v-bind:inset="item.inset"></v-divider>
                        <v-list-tile avatar v-else v-bind:key="item.title" @click="" router :to="item.link">
                            <v-list-tile-avatar>
                                <img v-bind:src="item.avatar"/>
                            </v-list-tile-avatar>
                            <v-list-tile-content>
                                <v-list-tile-title v-html="item.title"></v-list-tile-title>
                                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </template>
                </v-list>
            </v-card>
            <v-card v-else>
                <v-list>
                    <!--Todo: Layout com a lista das discipplinas que sao mostradas, falta colocar o scroll-->
                    <div v-for="item in menuItens">

                        <!--Todo: Segundo divider que poe a linha de divisao-->
                        <v-divider v-if="item.aditional"></v-divider>
                        <template>
                            <v-list-tile @click="">
                                <v-list-tile-action>
                                    <v-icon>{{ item.icon }}</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-content>
                                    <v-list-tile-title>
                                        {{ item.text }}
                                    </v-list-tile-title>
                                </v-list-tile-content>
                            </v-list-tile>
                        </template>
                    </div>
                </v-list>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
    export default {
        data () {
            return {
            }
        },
        computed: {
            explicadores () {
                return this.$store.getters.getExplicadores
            },
            isHomePage () {
                return this.$store.getters.isHomePage
            },
            menuItens () {
                return this.$store.getters.getMenuRightSide
            }
        },
        methods: {

        }
    }
</script>


