<template>
    <v-layout row>
        <v-flex xs12>
            <v-card xs12 class="mb-1 explicador-bar-fixed">
                <v-list class="text-xs-center"  three-line>
                    <h4>Explicadores > Matematica Basica</h4>
                </v-list>
                <v-list></v-list>
            </v-card>
            <v-card xs12 class="mt-5">
                <v-list three-line>

                    <template v-for="item in explicadores">
                        <v-subheader v-if="item.header" v-text="item.header"></v-subheader>
                        <v-divider v-else-if="item.divider" v-bind:inset="item.inset"></v-divider>
                        <v-list-tile avatar v-else v-bind:key="item.title" router :to="item.link" @click="goToProfile">
                            <v-list-tile-avatar>
                                <img v-bind:src="item.avatar"/>
                            </v-list-tile-avatar>
                            <v-list-tile-content>
                                <v-list-tile-title v-html="item.title"></v-list-tile-title>
                                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </template>
                </v-list>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>

    export default {
        beforeCreate () {
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
//            this.$Progress.finish()
        },
        data () {
            return {
            }
        },
        computed: {
            explicadores () {
                return this.$store.getters.getExplicadores
            }
        },
        methods: {
//          Alterna do menu principal para a pagina e menus do perfil
            goToProfile () {
                this.$store.dispatch('togleHomePage',false)
            }
        }
    }
</script>
<style scoped>
    .explicador-bar-fixed {
        position: fixed;
        z-index: 1;
        left: 0;
        width: 100%;
        /*left: 0;*/
    }
</style>


